@import "../sass-utils/mixnis";
@import "../sass-utils/vars";


.title-container {
	padding: 60px 0; }


.title-item {
   padding: 35px 0;
   text-align: center;
   border: 2px solid rgba(142, 142, 147, .9);
   border-radius: 8px;
   margin: 20px auto;
   transform: skew(2deg, 2deg);
   background: url("../../../img/bg-ask.jpg") no-repeat 50%/cover;
   box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22), inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1); }

.title-text {
   font-weight: 600;
   letter-spacing: .5px;
   font-size: 45px;
   @media(max-width: 550px) {
      font-size: 30px; } }

.jelly-fish-animate {
   width: 129px;
   height: 129px;
   position: absolute;
   transform: rotate(45deg);
   left: -15%;
   bottom: -15%;
   animation: jelly-fish-shift 35s linear infinite;
   @media (max-width: 900px) {
      animation-duration: 25s; }
   @media (max-width: 650px) {
      animation-duration: 20s; } }

@keyframes jelly-fish-shift {
   0 {
      bottom: -20%;
      left: -20%; }
   15% {
      bottom: -20%;
      left: -20%; }

   85% {
      bottom: 90%;
      left: 110%; }
   100% {
      bottom: 90%;
      left: 110%; } }

.jelly-fish-animate-near-btn {
   display: inline-block;
   width: 129px;
   height: 129px;
   .jelly-fish {
      animation-duration: 1.8s; } }

.jelly-fish {
   width: 100%;
   height: 100%;
   background: url("../../../img/jelly-fish.png");
   animation: jelly-fish 2s steps(9) infinite; }


@keyframes jelly-fish {
    from {
      background-position-x: 0; }

    to {
      background-position-x: -1163px; } }

.btn-box-title {
   display: flex;
   justify-content: center;
   align-items: center; }






